<template>
  <div class="app-page">
    <page-content class="page-content">
        <div class="top-search vertical-center level-center">
          <div @click="goHome" class="xiao-xi flex-center">
            <van-icon name="wap-home" />
          </div>
          <van-field
            clearable
            icon="search"
            placeholder="在这里输入搜索商品"
            @click-icon="$toast('question')"/>
        </div>
      <div class="container2">
        <van-pull-refresh v-model="isLoadingOnRefresh" @refresh="onRefresh">
          <van-list v-model="scrolLoading" :finished="scrolFinished" @load="onLoad" :immediate-check="false"
                    finished-text="没有更多了">
            <div class="c2-container">
              <div @click="goRecommendedDetail(1)" class="c2-container-content" v-for="(item,index) of list" :key="index">
                <div class="c2-container-content-icon">
                  <img class="c2-container-content-img" :src="item.imgUrl">
                </div>
                <div class="c2-container-content-goods1">
                  <div class="c2-container-content-goodsname">{{item.name}}</div>
                  <div class="c2-container-content-type">{{item.type}}</div>
                </div>
                <div class="c2-container-content-goods2">
                  <div class="c2-container-content-price">￥{{item.price}}</div>
                  <div class="c2-container-content-weight">{{item.weight}}</div>
                </div>
              </div>
            </div>
          </van-list>
        </van-pull-refresh>
      </div>
    </page-content>
  </div>
</template>

<script>
import PageContent from '@/components/page/content'
export default {
  name: 'search',
  components: {
    PageContent
  },
  data () {
    return {
      /** **** 滚动加载 ****/
      scrolLoading: false,
      scrolFinished: false,
      /** **** 上拉刷新 ****/
      isLoadingOnRefresh: false,
      /** ****测试数据****/
      list: [
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/yz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/category/mht.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/category/pg.png', name: '这是新鲜美味的水果', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/yz1.png', name: '这是新鲜美味的水果', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/category/mht.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/category/pg.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/yz1.png', name: '这是新鲜美味的水果', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/category/mht.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/category/pg.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '这是新鲜美味的水果', type: '绿色食品', price: '27.9', weight: '2.5kg'},
        {imgUrl: 'static/images/home/jz1.png', name: '新鲜的紫薯', type: '绿色食品', price: '27.9', weight: '2.5kg'}
      ],
      /** **** 测试数据 ****/
      test: '',
      count: 0
    }
  },
  methods: {
    goRecommendedDetail (id) {
      this.$router.push({path: '/category/productdetail', query: { id: id }})
    },
    goHome () {
      this.$router.push('/')
    },
    // 下拉刷新
    onRefresh () {
      setTimeout(() => {
        this.$toast('刷新成功')
        this.isLoadingOnRefresh = false
        for (let i = 0; i < this.list.length; i++) {
          this.test = this.list[i].name
          console.log('这是I的值', this.test)
        }
      }, 500)
    },
    // 滚动加载
    onLoad () {
      setTimeout(() => {
        this.loading = false
        if (this.list.length >= 40) {
          this.finished = true
        }
      }, 500)
    }

  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .top-search{
    width: 100%;
    height: 100px;
    background-color: rgba(5, 5, 5, 1);
    /deep/ .van-field{
      padding: 0 20px;
      background-color: $whiteffffff;
      line-height: 60px;
      width: 80%;height: 68px;
      border-radius: 0 10px 10px 0;
      .van-cell__value{
        background-color: $whiteffffff;
        display: flex;
        align-items: center;
        width: 100%;
        .van-field__body{
          width: 100%;
          font-size: 30px;
          input::-webkit-input-placeholder{
            font-family: $font-family-default;
            color: $black999999;
          }
          i{
            color: $black999999;
            font-size: 40px;
          }
        }
      }
    }
    .xiao-xi{
      background-color: $green22ac38;
      width: 90px;
      height: 68px;
      border-radius: 10px 0 0 10px;
      img{
        width: 80%;
      }
      i{
        color: $whiteffffff;
        font-size: 50px;
      }
    }
  }
  .container2{
    height: calc(100% - 100px);
    overflow: auto;
    .c2-container{
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      .c2-container-content{
        width: 49%;
        padding-top: 20px;
        .c2-container-content-icon{
          display: flex;
          justify-content: center;
          .c2-container-content-img{
            width: 330px;
            height: 280px;
          }
        }
        .c2-container-content-goods1{
          display: flex;
          justify-content: center;
          font-size: 30px;
          .c2-container-content-goodsname{
            padding-left: 30px;
            width: 160px;
            font-weight: bold;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-family: $font-family-Bold;
            color: $black333333;
          }
          .c2-container-content-type{
            padding-left: 8px;
            padding-right: 30px;
            font-weight: bold;
          }
        }
        .c2-container-content-goods2{
          display: flex;
          font-size: 30px;
          justify-content: center;
          padding-top: 20px;
          .c2-container-content-price{
            padding-left: 40px;
            width: 60%;
            color: #ff3b2f; // temp
          }
          .c2-container-content-weight{
            flex: 1;
            color: #999999;
          }
        }
      }
    }
  }

</style>



